<template>
    <div class="quiz__container">
        <h1 class="quiz__title">Frühlingsfest Quiz</h1>
        <div class="status__container">
            <div class="score__container">
                <p>Korrekt: {{ correctAnswers }}</p>
                <p>Falsch: {{ incorrectAnswers }}</p>
            </div>

            <CountdownTimer 
                :duration="3"
                @timer-end="handleTimeUp"
                ref="timer"
            />
        </div>
        
        <div class="question__container">
            <p class="question__title">{{ currentQuestionData.question }}</p>
            <div class="options__container">
                <BaseOption
                    v-for="(option, key) in currentQuestionData.options"
                    :key="key"
                    :label="key + '. ' + option"
                    :value="key"
                    :isSelected="selectedOptions.includes(key)"
                    @select="toggleOption"
                />
            </div>
        </div>
        <div class="confirm__btn">
            <BaseButton 
                :disabled="!canConfirm" 
                @click="confirmAnswer"
            >
                Bestätigen
            </BaseButton>
        </div>
    </div>
</template>

<script>
import BaseButton from './BaseButton.vue';
import BaseOption from './BaseOption.vue';
import CountdownTimer from './CountdownTimer.vue';
import questions from '@/assets/quiz.json';
console.log('Loaded questions:', questions);

export default ({
    name: 'QuizPage',
    components: {
        BaseButton,
        BaseOption,
        CountdownTimer
    },
    data() {
        return {
            questions,
            currentQuestion: 0,
            correctAnswers: 0,
            incorrectAnswers: 0, 
            selectedOptions: [],
            totalQuestions: 12,
            currentQuestionIndex: 0,
        };
    },
    computed: {
        currentQuestionData() {
            return this.questions[this.currentQuestion]; 
        },
        canConfirm() {
            // Make sure at least one option is selected
            return this.selectedOptions.length > 0;
        },
    },
    methods: {
        handleTimeUp() {
            // Calculate unanswered questions
            const remainingQuestions = this.totalQuestions - this.currentQuestionIndex;

            // Add remaining questions to incorrect answers
            this.incorrectAnswers += remainingQuestions;

            alert("Zeit ist um! Nicht beantwortete Fragen werden als falsch gewertet.");

            this.goToResults();
        },
        toggleOption(key) {
            // Option switching logic to handle single and multiple choices
            if (this.currentQuestionData.type === 'single') {
                this.selectedOptions = [key];
            } else {
                const index = this.selectedOptions.indexOf(key);
                if (index > -1) {
                    this.selectedOptions.splice(index, 1);
                } else {
                    this.selectedOptions.push(key);
                }
            }
        },
        confirmAnswer() {
            const correctAnswers = this.currentQuestionData.correctAnswer;
            const isCorrect = this.isAnswerCorrect(correctAnswers, this.selectedOptions);

            if (isCorrect) {
                this.correctAnswers++;
            } else {
                this.incorrectAnswers++;
            }

            this.selectedOptions = [];
            this.currentQuestionIndex++;

            if (this.currentQuestion < this.questions.length - 1) {
                this.currentQuestion++;
            } else {
                this.goToResults();
            }
        },
        isAnswerCorrect(correctAnswers, userAnswers) {
            
            return (
                userAnswers.length === correctAnswers.length &&
                correctAnswers.every((answer) => userAnswers.includes(answer))
            );
        },
        goToResults() {
            this.$router.push({
                name: 'ResultPage',
                query: {
                    correct: this.correctAnswers,
                    incorrect: this.incorrectAnswers,
                },
            }
            )},
    },
});
</script>

