<template>
    <div 
        :class="['option', { 'selected': isSelected }]"
        @click="handleSelect"
    >
        {{ label }}
    </div>
</template>

<script>
export default {
    name: 'BaseOption',
    props:{
        label: {
            type: String,
            required: true,
        },
        value: {
            type: String,
            required: true,
        },
        isSelected: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        handleSelect() {
            // Raise click event, parent component handles logic
            this.$emit('select', this.value);
        },
    },
};
</script>

<style>
.option {
    background-color: #F5EEDB;
    color: #2c3e50;
    font-size: 1.25rem;
    font-weight: 500;
    border: hsla(210, 30%, 24%, 0.102) 2px solid;
    border-radius: 10px;
    padding: 1rem 1.5rem;
    margin-block: 0.5rem;
    text-align: start;
    width: 100%;
}
.option:hover {
    background-color: hsl(44, 57%, 85%);
    color: #2c3e50;
    box-shadow: none;
}
.option.selected {
    background-color: hsl(44, 57%, 85%);
    color:  hsla(210, 30%, 24%, 0.4);
    box-shadow: none;
}

</style>