<template>
    <div class="countdown__timer">
        <p v-if="timeLeft > 0">Time Left <span class="timer__container">{{ formattedTime }}</span></p>
        <p v-else>Time's up!</p>
    </div>       
</template>

<script>
    export default {
        name: 'CountdownTimer',
        props: {
            duration: {
                type: Number,
                required: true,
                default: 180,
            },
        },
        data() {
            return {
                timeLeft: this.duration,
                timer: null,
            };
        },
        computed: {
            formattedTime() {
                const minutes = Math.floor(this.timeLeft / 60);
                const seconds = this.timeLeft % 60;
                return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
            }
        },
        methods: {
            startCountdown() {
                this.timer = setInterval(() => {
                    if (this.timeLeft > 0) {
                        this.timeLeft--;
                    } else {
                        clearInterval(this.timer);
                        this.$emit('timer-end');
                    }
                }, 1000);
            },
            stopCountdown() {
                if (this.timer) {
                    clearInterval(this.timer);
                    this.timer = null;
                }
            },
        },
        mounted() {
            this.startCountdown();
        },
        beforeUnmount() {
            this.stopCountdown();
        },
    };
</script>
<style>
.countdown__timer {
    font-size: 1.5rem;
    font-weight: 700;
    margin-block-end: 1.5rem;
}
.timer__container {
    font-size: 1.5rem;
    font-weight: 700;
    padding: 0.5rem 1rem;
    border-radius: 10px;
    color: #fdfbf7;
    background-color: #EC4C3E;
    margin-inline-start: 0.25rem;
}
</style>